import { Component, OnInit } from '@angular/core';
import { ProjectService } from '../shared/project.service';
import { Clipboard } from '@ionic-native/clipboard/ngx';
import { ToastController } from '@ionic/angular';


@Component({
  selector: 'app-invite-link',
  templateUrl: './invite-link.page.html',
  styleUrls: ['./invite-link.page.scss'],
})
export class InviteLinkPage implements OnInit {

  constructor(public projectService: ProjectService,
    public clipboard: Clipboard,
    public toastController: ToastController) { }

  ngOnInit() {
  }

  async presentToast(text, theme) {
    const toast = await this.toastController.create({
      message: text,
      showCloseButton: true,
      duration: 2000,
      position: 'top',
      closeButtonText: 'Done',
      color: theme
    });
    toast.present();
  }

  copy(){
    let selBox = document.createElement('textarea');
    selBox.style.position = 'fixed';
    selBox.style.left = '0';
    selBox.style.top = '0';
    selBox.style.opacity = '0';
    selBox.value = this.projectService.link.toString();
    document.body.appendChild(selBox);
    selBox.focus();
    selBox.select();
    document.execCommand('copy');
    document.body.removeChild(selBox);
    this.clipboard.copy(this.projectService.link.toString());
    this.presentToast('Ссылка скопирована', 'dark');
  }

}
